import React, {useState, useEffect} from 'react';
import {Button} from 'reactstrap';

import ComicDisplay from './ComicDisplay';
import APIKEY from '../helpers/environment';

const ComicResults = (props) => {
    const [comicResults, setComicResults] = useState([0]);
    const [offset, setOffset] = useState(0);

    const baseURL = 'https://gateway.marvel.com:443/v1/public/comics?';
    const apikey = APIKEY

    const fetchComicResults = () => {
        console.log(offset);
        let url = `${baseURL}titleStartsWith=${props.comicName}&offset=${offset}&apikey=${apikey}`;
        console.log(url);
        fetch(url)
        .then(result => result.json())
        .then(json => {console.log(json.data.results)})
        // console.log(comicResults);
    }

    useEffect(() => {
        fetchComicResults();
    }, [offset])

    const increaseOffset = () => {
        setOffset(offset + 20);
    }

    const decreaseOffset = () => {
        setOffset(offset - 20);
    }

    return(
        <div>
            {
                comicResults.length == 0 ? 
                    <p>No Results. Please Refresh The Page and Try Again.</p> 
                : <ComicDisplay comicResults={comicResults} searchBarOn={props.searchBarOn} searchBarOff={props.searchBarOff} detailView={props.detailView} setDetailView={props.setDetailView} />
            }
            <div className="navButtons">
                {offset > 0 ? <Button className="previousPage button" onClick={()=>{decreaseOffset()}}>Previous Page</Button> : null}
                {comicResults.length == 20 ? <Button className="nextPage button" onClick={()=>{increaseOffset()}}>Next Page</Button> : null}
            </div>
        </div>
    )
}

export default ComicResults;